import React, { useEffect } from "react";
import { CardMedia, useTheme } from "@mui/material";

import Navigation from "~/components/run/Navigation";

import styles from "./Survey.module.css";
import { buildResourceUrl } from "~/networking/common";
import { FORM_ID } from "~/constants/run";
import Group from "~/components/Group";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";

function Survey({ navigationIndex, survey, lang }) {
  const theme = useTheme();

  const ref = React.useRef();
  console.log(navigationIndex);

  useEffect(() => {
    if (ref.current) {
      ref.current.scrollTo({
        top: 0,
        behavior: "instant",
      });
    }
  }, [navigationIndex, ref.current]);

  const backgroundStyle = survey?.resources?.backgroundImage
    ? {
        backgroundImage: `url(${buildResourceUrl(
          survey?.resources.backgroundImage
        )})`,
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
      }
    : {};

  return (
    <DndProvider backend={HTML5Backend}>
      <form
        id={FORM_ID}
        ref={ref}
        className={styles.mainContainer}
        style={{
          backgroundColor: theme.palette.background.default,
          fontFamily: theme.textStyles.text.font,
          color: theme.textStyles.text.color,
          fontSize: theme.textStyles.text.size,
          ...backgroundStyle,
        }}
      >
        <div className={styles.surveyContent}>
          <div className={styles.surveyGroups}>
            {survey.resources?.headerImage ? (
              <CardMedia
                className={styles.cardImage}
                component="img"
                image={buildResourceUrl(survey.resources.headerImage)}
              />
            ) : null}
            {survey && survey.groups
              ? survey.groups
                  .filter((group) => group.inCurrentNavigation)
                  .map((group) => (
                    <Group key={group.code} group={group} lang={lang.code} />
                  ))
              : ""}
            <Navigation navigationIndex={navigationIndex} />
          </div>
        </div>
      </form>
    </DndProvider>
  );
}

export default Survey;
