import React, { useEffect, useMemo } from "react";
import { shallowEqual, useDispatch } from "react-redux";
import styles from "./RunSurvey.module.css";
import { useTranslation } from "react-i18next";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { CacheProvider } from "@emotion/react";

import {
  loadScript,
  startNavigation,
  continueNavigation,
} from "~/networking/run";
import { cacheRtl, rtlLanguage } from "~/utils/common";
import { defualtTheme } from "~/constants/theme";
import { stateReceived } from "~/state/runState";
import { useSelector } from "react-redux";
import { Box, Button, Toolbar, Typography } from "@mui/material";
import { useLocation, useNavigate } from "react-router-dom";
import { setFetching } from "~/state/templateState";
import Survey from "~/components/run/Survey";
import { PROCESSED_ERRORS, processError } from "~/utils/errorsProcessor";
import SurveyAppBar from "~/components/run/SurveyAppBar";
import SurveyDrawer, {
  COLLAPSE,
  COLLAPSE_IMMEDIATE,
  EXPAND,
} from "~/components/run/SurveyDrawer";
import LoadingDots from "~/components/common/LoadingDots";
import ErrorLayout from "~/components/common/ErrorLayout";
import { useService } from "~/hooks/use-service";
import { buildResourceUrl } from "~/networking/common";
import Image from "~/components/image/image";
import CompactLayout from "~/layouts/compact";

function RunSurvey({ preview, guest, mode, resume = false, responseId }) {
  const runService = useService("run");
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const lang = searchParams.get("lang");
  const [expanded, setExpanded] = React.useState(COLLAPSE);
  const [render, setRender] = React.useState(false);
  const [error, setError] = React.useState(false);
  const [inlineError, setInlineError] = React.useState(false);

  window["handleBack"] = () => {
    const result = expanded === EXPAND;
    setExpanded(COLLAPSE);
    return result;
  };

  const data = useSelector((state) => {
    return state.runState.data;
  }, shallowEqual);

  const isLoading = useSelector((state) => state.templateState.isLoading);

  const navigation = useSelector((state) => {
    return state.runState.navigation;
  }, shallowEqual);

  const { t, i18n } = useTranslation("run");
  const dispatch = useDispatch();

  useEffect(() => {
    if (navigation) {
      setExpanded(COLLAPSE_IMMEDIATE);
      continueNav(navigation, data.responseId);
    }
  }, [navigation]);

  useEffect(() => {
    if (rtlLanguage.includes(i18n.language)) {
      document.dir = "rtl";
    } else {
      document.dir = "ltr";
    }
  }, [i18n.language]);

  const handleError = (procesed) => {
    if (
      [
        PROCESSED_ERRORS.SURVEY_DESIGN_ERROR,
        PROCESSED_ERRORS.SURVEY_NOT_ACTIVE,
        PROCESSED_ERRORS.SURVEY_CLOSED,
        PROCESSED_ERRORS.SURVEY_EXPIRED,
        PROCESSED_ERRORS.SURVEY_SCHEDULED,
      ].indexOf(procesed) > -1
    ) {
      setInlineError(procesed);
    } else {
      setError(procesed);
    }
    dispatch(setFetching(false));
  };

  const startNav = () => {
    startNavigation(runService, lang, preview, guest, mode)
      .then((response) => {
        setRender(true);
        dispatch(stateReceived({ response, preview }));
        sessionStorage.setItem("responseId", response.responseId);
        i18n.changeLanguage(response.lang.code);
        dispatch(setFetching(false));
      })
      .catch((err) => {
        handleError(err);
      });
  };

  const continueNav = (payload, responseId) => {
    dispatch(setFetching(true));
    continueNavigation(runService, payload, responseId, preview, guest, mode)
      .then((response) => {
        setRender(true);
        dispatch(stateReceived({ response, preview }));
        sessionStorage.setItem("responseId", response.responseId);
        i18n.changeLanguage(response.lang.code);
        dispatch(setFetching(false));
        // document?.getElementById(FORM_ID)?.scrollTo(0, 0)
      })
      .catch((err) => {
        handleError(err);
      });
  };

  useEffect(() => {
    document.body.style.overflow = "visible";
    dispatch(setFetching(true));
    loadScript(runService, preview, guest)
      .then(() => {
        if (resume) {
          continueNav({ navigationDirection: { name: "RESUME" } }, responseId);
        } else {
          startNav();
        }
      })
      .catch((err) => {
        console.log("errori ", err);
        handleError(err);
      });
  }, []);

  const theme = React.useCallback(
    createTheme({
      ...defualtTheme(data?.survey?.theme),
      direction: rtlLanguage.includes(i18n.language) ? "rtl" : "ltr",
    }),
    [i18n.language, data?.survey?.theme]
  );

  const cacheRtlMemo = useMemo(() => cacheRtl(i18n.language), [i18n.language]);

  const navigate = useNavigate();

  const onBackClick = () => {
    if (window["Android"]) {
      window["Android"].onBackPressed();
    } else {
      navigate(-1);
    }
  };

  const showBack =
    window["Android"] || (mode == "offline" && navigate.length > 1);

  const backgroundStyle = data?.survey?.resources?.backgroundImage
    ? {
        backgroundImage: `url(${buildResourceUrl(
          data?.survey?.resources.backgroundImage
        )})`,
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        // backgroundSize: "100% 100%",
        backgroundPosition: "center",
      }
    : {};
  return (
    <>
      <CacheProvider value={cacheRtlMemo}>
        <ThemeProvider theme={theme}>
          {error && (
            <ErrorLayout
              error={error}
              setErrorSeen={() => {
                setError(null);
              }}
            />
          )}
          {isLoading ? <LoadingDots /> : null}
          {render && (
            <div
              className={styles.mainContainer}
              style={{
                backgroundColor: theme.palette.background.default,
                fontFamily: theme.textStyles.text.font,
                color: theme.textStyles.text.color,
                fontSize: theme.textStyles.text.size,
                ...backgroundStyle,
              }}
            >
              <SurveyMemo
                key="Survey"
                survey={data?.survey}
                navigationIndex={data?.navigationIndex}
                lang={data?.lang}
                additionalLang={data?.additionalLang}
              />
            </div>
          )}
        </ThemeProvider>
      </CacheProvider>
      {inlineError && (
        <Box style={{ height: "100%", overflow: "auto" }}>
          <CompactLayout>
            <Typography variant="h3" paragraph>
              {t("error")}
            </Typography>
            <Typography sx={{ color: "text.secondary" }}>
              {t("processed_errors." + inlineError.name)}
            </Typography>
            <Image
              alt="500"
              src="/illustration_500.svg"
              sx={{
                mx: "auto",
                maxWidth: 320,
                my: { xs: 5, sm: 8 },
              }}
            />
            <Button
              fullWidth
              size="large"
              color="inherit"
              variant="contained"
              className={styles.goBack}
              onClick={() => navigate(-1)}
            >
              {t("goBack")}
            </Button>
          </CompactLayout>
        </Box>
      )}
    </>
  );
}

const SurveyMemo = React.memo(Survey);

export default RunSurvey;
